@import "../../style/variables.scss";

.char {
  &__search {
    width: 425px;
    min-height: 127px;
    background: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
    margin-top: 30px;
    &_form {
      display: grid;
      grid-template-columns: minmax(50px, auto) minmax(50px, auto);
      padding: 25px;
      &-label {
        grid-column: 1 / 3;
        width: 204px;
        height: 24px;
        font-family: "Roboto Condensed";
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        line-height: 21px;
        color: #000000;
      }
      &-input[type="text"] {
        font-family: inherit;
        font-size: inherit;
        line-height: inherit;
        width: 250px;
        height: 38px;
        padding: 10px;
        background: #ffffff;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border: none;
        margin-top: 15px;
      }
      &-buttonMain {
        grid-column: 2 / 3;
        grid-row: 2 / 3;
        margin-left: 24px;
        width: 101px;
        height: 38px;
        margin-top: 15px;
      }
      &-buttonSecondary {
        grid-column: 2 / 3;
        grid-row: 3 / 4;
        margin-left: 24px;
        width: 101px;
        height: 38px;
        margin-top: 15px;
      }
    }
    &-message {
      display: grid;
    }
  }
}
